import Raven from 'raven-js';

import {addEvent, toggleClass} from './helpers';
import {
    GenericView, GenericViewFlipable, GenericViewOffline
} from './GenericView';
import {GenericComponent} from './GenericComponent';

/* -------------------------------------------------------------------------- */

export class payment extends GenericViewFlipable {
    constructor(uri, app, className, data) {
        super(uri, app, (className || 'payment'), data);
    }

    show(callback) {
        this.getBackURI();
        if (this.back_url) {
            this.render();
        }
        super.show(callback)
    }

    getBackURI() {
        let back_url = '';
        if (this.back_url) {
            this.data.back_url = this.back_url;
            return;
        }

        if (this.app.history && this.app.history.length) {
            back_url = this.app.history.slice(-1)[0];
        }

        if (back_url && back_url.match(/seatmap/)) {
            this.back_url = this.data.back_url = back_url;
        }
    }

    render() {
        if (!this.data) {
            this.data = {};
        }
        this.getBackURI();
        this.data.year = [];
        this.data.month = [];
        for (var i = 1; i <= 12; i++) {
            this.data.month.push({
                value: i,
                view: i > 9 ? i : `0${i}`
            });
        }
        const cur_year = (new Date()).getFullYear() - 2000;
        for (var i = cur_year; i <= cur_year + 10; i++) {
            this.data.year.push({
                value: i,
                view: i > 9 ? i : `0${i}`
            });
        }
        super.render();
    }
    delete() {
        const uri = this.uri;
        const app = this.app;
        super.delete(() => {app.get(uri)});
    }

    formSaved() {
        super.formSaved();
        let all_data = true;
        Object.keys(this.data.data).forEach(key => {
            all_data = all_data && this.data.data[key];
        })
        if (all_data && this.back_url) {
            const seatmap = this.app.cache[this.back_url];
            seatmap.data.data.has_card_info = true;
            seatmap.render();
        }
    }
}

/* -------------------------------------------------------------------------- */

export class user extends GenericViewFlipable {
    constructor(uri, app, className, data) {
        super(uri, app, (className || 'user'), data);
    }
    converter(data) {
        if (data.user_request) {
            const user_request = {};
            Array.prototype.forEach.call(data.user_request, (req) => {
                if ('phone' in req || 'phone_code' in req || 'phone_country' in req) {
                    user_request.phone = true;
                }
            });
            data.user_request = user_request;
        }
        return data;
    }
    initForm() {
        super.initForm();
        addEvent(this.view.querySelector('[data-bind="btn-logout"]'), 'click', () => {
            this.app.platform.logout();
        });
    }
}

/* -------------------------------------------------------------------------- */

export class password extends GenericView {
    constructor(uri, app, className, data) {
        super(uri, app, (className || 'password'), data);
    }
    get(forceUpdate, callback, fail) {
        this.data = {data: {}}
        if (typeof(callback) === 'function') {
            callback();
        }
        return this;
    }
    render() {
        super.render()
        this.view.querySelector('[name="password"]').focus()
    }
    formSubmit(form, event) {
        event.preventDefault();
        const that = this;
        const confirmation = this.view
            .querySelector('[data-name="confirmation"]').value;
        const data = that.serializeChanges(form);

        this.data.data = {
            password: data['password'],
            confirmation
        }
        if (data['password'] != confirmation) {
            this.data.confirmation_error = true;
            this.render();
            return;
        }
        else {
            this.data.confirmation_error = false;
        }
        that.loading(true);
        this.app.ajax({
            url: this.app.settings.endpoints + this.uri,
            method: 'PUT',
            data:{data},
            callback(data) {
                that.loading(false);
                if (data.result) {
                    that.render();
                    that.formSaved();
                }
                else {
                    that.submitOnError(form, status, data);
                }
            },
            error(status, data) {
                that.submitOnError(form, status, data);
            }
        });
    }
}

/* -------------------------------------------------------------------------- */

export class verification extends GenericViewOffline {
    constructor(uri, app, className, data) {
        super(uri, app, data);
        this.init();
    }
    init() {
        const that = this;
        const view = document.querySelector('#verification');
        if (!view) {
            return true;
        }
        const sentMsg = view.querySelector('.alert');
        addEvent(this.view.querySelector('form'), 'submit', function (event) {
            event.preventDefault();
            const input = this.verification;
            const msg = input.value;
            if ((`${msg}`).length < 5 || !(`${msg}`).match(/^\w+$/)) {
                that.super.inputToggleClass.call(input, 'has-error', true);
                this.toolTip(input, input.dataset.errorMsg);
                return;
            }
            that.super.inputToggleClass(input, 'has-error', false);
            window.location.href = `${window.location.search}verification/${msg}/`;
        });
        addEvent(this.view.querySelector('.btn-default'), 'click', function (event) {
            const btn = this;
            btn.disabled = true;
            app.ajax({
                url: `${app.settings.endpoints}/send_verification/`,
                method: 'POST',
                callback() {
                    btn.disabled = false;
                    sentMsg.classList.add('in');
                },
                error(status, data) {
                    btn.disabled = false;
                    that.super.getOnError(status, data);
                }
            });
        });
        return false;
    }
}


/* -------------------------------------------------------------------------- */

export class error extends GenericComponent {
    constructor(uri, app, className, data) {
        super(uri, app, (className || 'error'), data);
        this.auto_destroy = true;

        if (!data.message && data.data) {
            if (data.data.message) {
                this.data.message = this.data.data.message;
                this.data.message_human = this.data.data.message_human;
                this.data.title = '';
            } else {
                this.data.html = this.data.data;
            }
        }

        if (data && !data.expected && window.Raven) {
            Raven.captureException(new Error(`${this.data.title}\n${this.data.message}`));
        }
    }
    toggle(state, callback) {
        this.view.querySelectorAll('.fade').forEach(fade => {
            toggleClass(fade, 'in', state);
        });
        setTimeout(callback, 500);
    }
    bindLinks() {
        super.bindLinks();
        this.toggle(true);
        addEvent(this.view.querySelectorAll('[data-dismiss="modal"]'), 'click', () => {
            this.toggle(false, () => {
                this.destroy();
            });
        })
    }
    destroy() {
        this.view.parentElement.removeChild(this.view);
        delete this.app.cache[this.uri];
    }
}

